export default function scrollLock(view) {
  if (view) {
    document.body.style.position = 'fixed';
    document.body.style.overflow = 'hidden';
    document.body.style.width = '100%';
    document.body.style.height = '100%';
    // document.body.addEventListener('scroll', lockHandler);
    // document.body.addEventListener('touchmove', lockHandler);
    // document.body.addEventListener('mousewheel', lockHandler);
  } else {
    document.body.style.position = 'static';
    document.body.style.overflow = 'auto';
    document.body.style.height = 'auto';
    // document.body.removeEventListener('scroll', lockHandler);
    // document.body.removeEventListener('touchmove', lockHandler);
    // document.body.removeEventListener('mousewheel', lockHandler);
  }
}
